<template>
    <div>
        <header>
           <div class="location">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="icon-location"><path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z">
                    </path>
                </svg>
                <span>保定市河北软件职业技术学院</span>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="icon-arrow"><path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z">
                    </path>
                </svg>
           </div>
        </header>
        <section class="search">
            <div class="button">
                <a href="">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path fill-opacity=".38" d="M14.778 13.732a.739.739 0 1 1-1.056 1.036l-2.515-2.565a.864.864 0 0 1-.01-1.206 4.894 4.894 0 0 0 1.357-3.651c-.126-2.492-2.156-4.52-4.648-4.647a4.911 4.911 0 0 0-5.16 5.163c.126 2.475 2.13 4.496 4.605 4.642.451.026.896-.008 1.326-.1a.739.739 0 0 1 .308 1.446c-.56.12-1.137.164-1.72.13-3.227-.19-5.83-2.815-5.995-6.042a6.39 6.39 0 0 1 6.71-6.715c3.25.165 5.884 2.8 6.05 6.048a6.37 6.37 0 0 1-1.374 4.3l2.12 2.161z">
                        </path>
                    </svg>
                    <span>搜索饿了么商家、商品名称</span>
                </a>
            </div>
        </section>
        
        <section class="hot-search">
            <ul>
                <li v-for="(item,index) in hotsearches" :key="index">{{item.word}}</li>
            </ul>
        </section>
        
        <section class="entries">
            <svg v-if="entries.length==0" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 1080 490">
                <defs>
                <path id="b" d="M0 0h1080v489H0z"/>
                <filter id="a" width="200%" height="200%" x="-50%" y="-50%" filterUnits="objectBoundingBox">
                <feOffset dy="1" in="SourceAlpha" result="shadowOffsetOuter1"/>
                <feColorMatrix in="shadowOffsetOuter1" values="0 0 0 0 0.933333333 0 0 0 0 0.933333333 0 0 0 0 0.933333333 0 0 0 1 0"/>
                </filter>
                </defs>
                <g fill="none" fill-rule="evenodd">
                <g>
                <use fill="#000" filter="url(#a)" xlink:href="#b"/>
                <use fill="#FFF" xlink:href="#b"/>
                </g>
                <g fill="#F6F6F6">
                <g transform="translate(76 36)">
                <path d="M9 139h100v34H9z"/>
                <ellipse cx="59" cy="59" rx="59" ry="59"/></g>
                <g transform="translate(346 36)">
                <path d="M9 139h100v34H9z"/><ellipse cx="59" cy="59" rx="59" ry="59"/>
                </g>
                <g transform="translate(616 36)">
                <path d="M9 139h100v34H9z"/>
                <ellipse cx="59" cy="59" rx="59" ry="59"/>
                </g>
                <g transform="translate(886 36)">
                <path d="M9 139h100v34H9z"/>
                <ellipse cx="59" cy="59" rx="59" ry="59"/>
                </g>
                </g>
                <g fill="#F6F6F6">
                <g transform="translate(76 252)">
                <path d="M9 139h100v34H9z"/>
                <ellipse cx="59" cy="59" rx="59" ry="59"/></g><g transform="translate(346 252)"><path d="M9 139h100v34H9z"/><ellipse cx="59" cy="59" rx="59" ry="59"/></g><g transform="translate(616 252)"><path d="M9 139h100v34H9z"/><ellipse cx="59" cy="59" rx="59" ry="59"/></g><g transform="translate(886 252)"><path d="M9 139h100v34H9z"/><ellipse cx="59" cy="59" rx="59" ry="59"/>
                </g>
                </g>
                </g>
            </svg>
            <mt-swipe :auto="4000">
                <mt-swipe-item v-for="(item,index) in entries" :key="index">
                    <a href="" v-for="(item2,index2) in item" :key="index2">
                        <img :src="item2.image_hash | imgUrl" alt="">
                        <span>{{item2.name}}</span>
                    </a>
                </mt-swipe-item>
            </mt-swipe>
        </section>
        
        <section class="recommends">
            <section class="shop-title">
                <h3>推荐商家</h3>
            </section>
            <!-- 商家列表 -->
            <section class="shop-list" v-for="(item,index) in restaurants" :key="index">
                <div class="shop">
                    <!-- 左面logo -->
                    <div class="shop-logo">
                        <img :src="item.restaurant.image_path | imgUrl">
                    </div>
                    <!-- 右面商家详细信息 -->
                    <div class="shop-main">
                        <!-- 商品名称 -->
                        <div class="title">
                            <h3>{{item.restaurant.name}}</h3>
                        </div>
                        <!-- 商品评分 -->
                        <div class="rating">
                            <div class="ratingWrap">
                                <div class="star">
                                     <elm-star :score="item.restaurant.rating" :size="24"></elm-star>
                                </div>
                                <span>{{item.restaurant.rating}}</span>
                                <span class="dan">{{item.restaurant.recent_order_num}}单</span>
                            </div>
                            <div class="deliver-mode">
                                <span>蜂鸟专送</span>
                            </div>
                        </div>
                        <!-- 配送费、配送时间 -->
                        <div class="fee">   
                            <div class="deliver-fee">
                                <span>￥{{item.restaurant.float_minimum_order_amount}}起送</span>
                                <span>优惠配送费{{item.restaurant.delivery_fee_discount}}</span>
                            </div>
                            <div class="distance">
                                <span>{{item.restaurant.distance / 1000 | toFix}}km</span>
                                <span>{{item.restaurant.order_lead_time}}分钟</span>
                            </div>
                        </div>
                        <!-- 口碑人气
                        <div class="tag-line">
                            <span>狗不理包子</span>
                            <span>
                                <img src="" alt="">
                                <span>口碑人气好店</span>
                            </span>
                        </div> -->
                        <!-- 商家活动 -->
                        <div class="activities">
                            <div class="activityList" v-for="(item,index) in item.restaurant.activities" :key="index">
                                <span style="background-color: rgb(240, 115, 115);color:#fff">{{item.icon_name}}</span>
                                <span>{{item.tips}}</span>
						    </div>
                            <div class="activity-btn">
                                <span>3个活动</span>
                                <img src="" alt="">
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </section>
        <elm-tabbar></elm-tabbar>
    </div>
</template>

<script>
    import Tabbar from '@/components/footer/Tabbar'
    import {getHotSearches,getEntries,getRestaurants} from '@/api/msite'
    import Star from '@/components/star/Star'
    import _ from "lodash"
   
    import Vue from "vue"
    import { Swipe, SwipeItem } from 'mint-ui';

    import "mint-ui/lib/style.css"
    import "mint-ui/lib/index.js"

    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);
   
    export default {
        components:{
           'elm-tabbar': Tabbar,
           'elm-star': Star
        },
        data(){
            return{
                hotsearches:[],
                entries:[],
                restaurants:[]
            } 
        },
        //过滤器
        filters:{
            imgUrl(src){
                let url = "https://fuss10.elemecdn.com/";
                //造筛子
                let reg = /jpeg|jpg|png|gif$/;
                //筛图片后缀
                let res = src.match(reg);
                // console.log(res[0]);
                url += src.slice(0,1)+"/"+src.slice(1,3)+"/"+src.slice(3)+"."+res[0];
                url += "?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"
                return url;
            },
            //保留两位
            toFix(data){
                return data.toFixed(2);
            }

        },
        created(){
            getHotSearches()
            .then((data) => {
                // console.log(data.data)
                this.hotsearches = data.data;
            })
            .catch((err) => {
                //  console.log(err)
            })
            //获取分类轮播数据
            getEntries()
            .then((data) => {
                //console.log(data.data[0].entries);
                this.entries = _.chunk(data.data[0].entries, 8);
                // console.log(this.entries);
            })
            .catch((err) => {
                // console.log(err);
            })
            //获取商家列表
            getRestaurants()
                .then((res) => {
                    
                    this.restaurants = res.data.items;
                    console.log(this.restaurants)
                })
                .catch((err) => {
                    // console.log(err);
                })            
        }
    }
</script>
<style lang="less" scoped>
    *{box-sizing:border-box}
    header{
        height: 1.333333rem;
        background-image: linear-gradient(90deg,#0af,#0085ff);
        display: flex;
        align-items: center;
        padding-left: 0.346667rem;
        .location{
            display: flex;
            align-items: center;
            .icon-location{
                width: 0.4rem;
                height: 0.426667rem;
            }
            .icon-arrow{
                width: 0.186667rem;
                height: 0.106667rem;
            }
            span{
                font-size: 0.453333rem;
                color: #fff;
                width: 80%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                margin-left: 0.133333rem;
                font-weight: 700;
            }
        }
        
    }
    .search{
        display: flex;
        width: 100%;
        justify-content: center;   
        align-items: center;
        position: sticky;
        top: 0;
        width: 100%;
        z-index: 999;
        padding: 0 0.346667rem 0.346667rem;
        background-image: linear-gradient(90deg,#0af,#0085ff);
        
        .button{
            width: 100%;
            height: 0.986667rem;
            line-height: 0.986667rem;
            background: #fff;
            a{
                display: flex;
                align-items: center;
                justify-content: center;
                text-decoration: none;
                svg{
                    width: 0.386667rem;
                    height: 0.386667rem;
                }
                span{
                    font-size: 0.373333rem;
                    color: #999;
                    margin-left: 0.16rem;
                }
            }
        }
        
    }
    .hot-search{
        height: 1.12rem;
        background-image: linear-gradient(90deg,#0af,#0085ff);
        width: 100%;
        padding: 0.0rem 0.346667rem;
        ul{
            width: 100%;
            overflow-x: auto;
            white-space: nowrap;
            &::-webkit-scrollbar{
                display: none
            }
            li{
                font-size: 0.32rem;
                display:inline-block;
                color:#fff;
                &:not(:last-child){
                    margin-right: 0.48rem;
                }
            }
        }
    }
    .entries{
        height: 4.533333rem;
        a{
            text-decoration: none;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 25%;
            float: left;
            margin-top: 0.373333rem;
            img{
                width: 0.853333rem;
                height: 0.853333rem;
            }
            span{
                font-size: 0.32rem;
                color: #666;
                margin-top: 0.266667rem;
            }
        }
    }
    //推荐商家
    .recommends{
        .shop-title{
            font-size: 0.4rem;
            height: 1.333333rem;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 20px;
            &:before,&:after{
                content:"";
                display: block;
                width: 0.533333rem;
                height: 0.026667rem;
                background: #999;
            }
            &:before{
                margin-right: 0.346667rem;
            }
            &:after{
                margin-left: 0.346667rem;
            }
        }
        .shop-list{
            margin-top: .533333rem;
            .shop{
                display: flex;
                justify-content: flex-start;
                .shop-main{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    flex-grow: 1;
                    .title{
                        h3{
                            font-size: .4rem;
                            font-weight: 700;
                        }
                    }
                    .rating{
                        display: flex;
                        justify-content: space-between;
                        margin-top: .266667rem;
                        span{
                            font-size: 0.266667rem;
                        }
                        .star{
                           display: flex;
                        }
                        .dan{
                            margin-left: .533333rem;
                        }
                        .ratingWrap{
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }
                        .deliver-mode{
                            background-image: linear-gradient(90deg,#0af,#0085ff);
                            color:#fff;
                            font-size: 0.266667rem;
                            padding: 0.026667rem;
                            span{
                                background-image: linear-gradient(45dge,#0085ff,#0af);
                                color: #fff;
                            }
                        }
                    }
                    .fee{
                        display: flex;
                        justify-content: space-between;
                        margin-top: .266667rem;
                        .deliver-fee{
                            display: flex;                         
                        }
                        .distance{
                            display: flex;                          
                        }
                        span{
                            font-size: 0.266667rem;
                        }
                    }
                    .activities{
                        
                        justify-content: space-between;
                        font-size: .32rem;
                        margin-top: .266667rem;
                        .activity-list{
                            margin-top: -0.266667rem;
                            span{
                                font-size:0.293333rem;

                            }
                        }
                        .activity-btn{
                            margin-top: -0.266667rem;
                            text-align: right;
                            span{
                                font-size: 0.24rem;
                            }
                        }
                    }
                }
            }
        }
    }
</style>




